						"https://s3.ax1x.com/2021/03/12/6UXEZ9.jpg"
					],
					spec: [
						{
							name: '清新帮纸尿裤NB76片',
							list: [{
								price: '200.05',
								img: 'https://s3.ax1x.com/2021/03/12/6UXiMF.jpg'
							}]
						},
						{
							name: '清新帮纸尿裤M72片',
