</script>

<style scoped lang="less">
	.wrap {
		height: 100vh;
		background: #F3F3F3;
		// z-index: -1;
	}

	.scrollHeight {
		height: calc(100vh - var(--status-bar-height) - 150rpx);
	}

	.specActive {
		background-color: #fc979f;
		color: #fff;
	}

	.space {
		background-color: #f3f3f3;
	}

	/deep/ .uni-scroll-view::-webkit-scrollbar {
		/* 隐藏滚动条，但依旧具备可以滚动的功能 */
		display: none
	}
</style>
